@import "../../assets/styles/settings.scss";
@import "../../assets/styles/breakpoints.scss";

.navigation {
  display: flex;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 80px;
  background-color: $color-primary;
  border-bottom: 1px solid $color-white;
  z-index: 1;
  white-space: nowrap;
  transition: $transition-toggle-nav;
  &__logo {
    padding: 10px 0;
    margin-left: 3vw;
  }
  &__image {
    width: 160px;
  }
  &__list {
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 100%;
  }
  &__list-item {
    padding: 28px 2vw;
    color: $color-white;
    font-weight: bold;
    text-decoration: none;
    border-bottom: 4px solid $color-primary;
    transition: $transition-navigation;
    &:hover {
      background-color: $color-primary-hovered;
      border-bottom-color: $color-primary-hovered;
    }
    &.active {
      color: $color-tan;
      border-bottom-color: $color-tan;
    }
  }
  &__ligue1 {
    margin-right: 2vw;
  }
  &--hide {
    transform: translate(0, -80px);
  }
}

@media (max-width: $screen-lg) {
  .navigation {
    height: 194px;
    flex-direction: column;
    &__logo {
      margin-left: 0;
    }
    &--hide {
      transform: translate(0, -194px);
    }
  }
}

@media (max-width: $screen-md) {
  .navigation {
    height: 510px;
    position: absolute;
    &__list {
      flex-direction: column;
      width: 100%;
    }
    &__list-item {
      width: 80%;
      text-align: center;
    }
    &__ligue1 {
      margin-right: 0;
    }
    &--hide {
      transform: none;
    }
  }
}
